import React, { Component } from 'react';
import styled, { css } from 'styled-components';
import { flexCenter } from './../utils';
import { DOWN_ARROW } from './../images';
// components
import { SectionWrap, ScrollFooter, Link, Logo } from './../components/common';

class Intro extends Component {
  render = () => {
    return (
      <IntroWrap>
        <div>
          <LargeText data-aos="fade-down">HELLO</LargeText>
          <MediumText data-aos="fade-left">I'M RYAN</MediumText>
          <SmallText data-aos="fade-right">I'M A WEB DEVELOPER</SmallText>
        </div>
        <ScrollFooter>
          <Link linkstyles={linkstyles} to="About">
            About
            <Logo src={DOWN_ARROW} alt="down" />
          </Link>
        </ScrollFooter>
      </IntroWrap>
    );
  };
}

const linkstyles = css`
  width: 100px;
  background: ${({ theme }) => theme.color.secondary};
  flex-direction: column;
  &:hover {
    opacity: 0.8;
  }
`;

const IntroWrap = styled(SectionWrap)`
  ${flexCenter};
  background: ${({ theme }) => theme.color.primary};
  color: ${({ theme }) => theme.color.light};
  margin-bottom: 30%;
`;

const Text = styled.div`
  color: ${({ theme }) => theme.color.light};
`;

const LargeText = styled(Text)`
  font-size: 7em;
  letter-spacing: 0.12em;
  font-weight: 700;
  padding-left: 0px;
`;

const MediumText = styled(Text)`
  font-size: 5em;
  font-weight: 500;
  letter-spacing: 0.13em;
  margin-top: 1%;
`;

const SmallText = styled(Text)`
  font-size: 1.5em;
  letter-spacing: 0.32em;
  padding-left: 1%;
  margin-top: 6%;
`;

export default Intro;
